import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { v4 as uuidv4 } from "uuid";
import { Link, useParams, useNavigate } from "react-router-dom";
import Button from "../../styled-components/Button";
import "../css/ProjectForm.css";
import CustomNavbar from "../../styled-components/Navbar";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";

export default function ProjectForm() {
  // Get the project ID from the URL params
  const { id } = useParams();
  const navigate = useNavigate();

  // State for selected users
  const [selectedUsers, setSelectedUsers] = useState([]);
  const [snackbarOpen, setSnackbarOpen] = useState(false);


  // Form handling using react-hook-form
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    watch,
  } = useForm();

  // Watch the 'startDate' and 'endDate' fields to trigger validation when they change
  const watchStartDate = watch("startDate", "");
  const watchEndDate = watch("endDate", "");

  // Use useEffect to prefill form data when editing a project
  useEffect(() => {
    prePopulateData(id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, reset]);

  // Use useEffect to trigger validation whenever the start date changes
  useEffect(() => {
    validateEndDate(watchEndDate);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [watchStartDate, watchEndDate]);

  // Custom validation function to check if the end date is after the start date
  const validateEndDate = (endDate) => {
    return (
      (endDate && endDate >= watchStartDate) ||
      "End date must be after the start date"
    );
  };

  const prePopulateData = (id) => {
    if (id) {
      const item = localStorage.getItem("projectData");
      if (item) {
        const projectData = JSON.parse(item);
        const projectToEdit = projectData.find((project) => project.id === id);
        if (projectToEdit) {
          // Reset the form with project data and set selected users
          console.log(projectToEdit);
          reset(projectToEdit);
          setSelectedUsers(projectToEdit.selectedUsers || []);
        }
      }
    }
  };

  // Get user data from localStorage
  const userData = JSON.parse(localStorage.getItem("userData")) || [];

  // Form submission handler
  const onSubmit = async (data) => {
    // Get existing project data or an empty array
    const existingProjectData =
      JSON.parse(localStorage.getItem("projectData")) || [];

    if (id) {
      // Update an existing project if editing
      const updatedProjectData = existingProjectData.map((project) =>
        project.id === id ? { ...project, ...data, selectedUsers } : project
      );
      localStorage.setItem("projectData", JSON.stringify(updatedProjectData));
    } else {
      // Create a new project if not editing
      const newProject = { ...data, id: uuidv4(), selectedUsers };
      const updatedProjectData = [...existingProjectData, newProject];
      localStorage.setItem("projectData", JSON.stringify(updatedProjectData));
    }

    // Reset the form and navigate to the projects page
     setSnackbarOpen(true);
    const delayDuration = 2000; // Adjust the duration as needed
    await new Promise((resolve) => setTimeout(resolve, delayDuration));
    navigate(-1);
  };

  const handleUserSelectChange = (e) => {
    const selectedUserIds = Array.from(
      e.target.selectedOptions,
      (option) => option.value
    );

    setSelectedUsers(selectedUserIds);

    // Update userData in local storage
    console.log("userData", userData);

    const updatedUserData = userData.map((user) => ({
      ...user,
      projects: user.projects
        ? selectedUserIds.includes(user.id)
          ? [...user.projects, id]
          : user.projects.filter((projectId) => projectId !== id)
        : [], // Ensure user.projects is initialized as an array
    }));

    localStorage.setItem("userData", JSON.stringify(updatedUserData));
  };

  // Display error messages for form fields
  const displayError = (fieldName) => {
    return errors[fieldName] ? (
      <p className="error-message">{errors[fieldName].message}</p>
    ) : null;
  };

  // CSS class for selected user tags
  const selectedUserTagClass = "selected-user-tag";
    const handleSnackbarClose = (event, reason) => {
      if (reason === "clickaway") {
        return;
      }

      setSnackbarOpen(false);
    };


  return (
    <>
      <CustomNavbar />
      <br />
      <h1>{id ? "Edit Project" : "Add New Project"}</h1>

      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="form-element">
          <label>Project Name</label>
          <input
            type="text"
            name="name"
            placeholder="Enter project name"
            {...register("name", { required: "Project name is required" })}
          />
          {displayError("name")}
        </div>
        <div className="form-element">
          <label>Description</label>
          <textarea
            name="description"
            placeholder="Enter project description"
            {...register("description", {
              required: "Project description is required",
            })}
          />
          {displayError("description")}
        </div>
        <div className="form-element">
          <label>Start Date</label>
          <input
            type="date"
            name="startDate"
            {...register("startDate", { required: "Start date is required" })}
          />
          {displayError("startDate")}
        </div>
        <div className="form-element">
          <label>End Date</label>
          <input
            type="date"
            name="endDate"
            {...register("endDate", {
              required: "End date is required",
              validate: validateEndDate, // Use the custom validation function
            })}
          />
          {displayError("endDate")}
        </div>
        <div className="form-element">
          <label>Priority</label>
          <select
            name="priority"
            {...register("priority", { required: "Priority is required" })}
          >
            <option value="high">High</option>
            <option value="medium">Medium</option>
            <option value="low">Low</option>
          </select>
          {displayError("priority")}
        </div>
        <div className="form-element">
          <label>Tech Stack</label>
          <input
            type="text"
            name="techStack"
            placeholder="Enter project tech stack"
            {...register("techStack", { required: "Tech stack is required" })}
          />
          {displayError("techStack")}
        </div>
        <div className="form-element">
          <label>
            Assign Users:
            <br /> *Note: Hold CTRL to select multiple users
          </label>
          <select
            name="selectedUsers"
            multiple
            {...register("selectedUsers")}
            onChange={handleUserSelectChange}
            value={selectedUsers}
          >
            {userData.map((user) => (
              <option key={user.id} value={user.id}>
                {user.name}
              </option>
            ))}
          </select>
          {displayError("selectedUsers")}
        </div>

        <div className="form-element">
          <label>Selected Users:</label>
          <div className="selected-users-tags">
            {selectedUsers.map((userId) => {
              const user = userData.find((u) => u.id === userId);
              return (
                <span key={userId} className={selectedUserTagClass}>
                  {user.name}
                </span>
              );
            })}
          </div>
        </div>
        <div className="form-element">
          <div className="button-wrapper">
            <Button type="submit">{id ? "Save" : "Submit"}</Button>
            {id && (
              <Link to="/projects">
                <Button>Cancel</Button>
              </Link>
            )}
          </div>
        </div>
      </form>
        <Snackbar
        open={snackbarOpen}
        autoHideDuration={6000}
        onClose={handleSnackbarClose}
      >
        <MuiAlert
          elevation={6}
          variant="filled"
          onClose={handleSnackbarClose}
          severity="success"
        >
          {id ? "Project updated successfully!" : "Project added successfully!"}
        </MuiAlert>
      </Snackbar>
    </>
  );
}
