import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
import Login from "../presentation/Login";
import dummyProjects from "../../project-component/projectArray";
import userArray from "../../user-component/userArray";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";

const LoginContainer = ({ setUserIsLoggedIn }) => {
  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
  } = useForm();
  const navigate = useNavigate();
  const [errorMessage, setErrorMessage] = useState("");
  const [snackbarOpen, setSnackbarOpen] = useState(false);

  useEffect(() => {
    getProjectData();
    getUserData();
  }, []);

  const handleSnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setSnackbarOpen(false);
  };

  const onSubmit = async (data) => {
    const { username, password } = data;
    const users = JSON.parse(localStorage.getItem("userData"));
    const lowercaseUsername = username.toLowerCase();
    const foundUser = users.find(
      (user) =>
        (user.name.toLowerCase() === lowercaseUsername ||
          user.email.toLowerCase() === lowercaseUsername) &&
        user.password === password
    );

    if (foundUser) {
      localStorage.setItem("loggedInUser", JSON.stringify(foundUser));
      setSnackbarOpen(true); // Display snackbar for successful login
      const delayDuration = 1000; // Adjust the duration as needed
      await new Promise((resolve) => setTimeout(resolve, delayDuration));

      navigate("/user/" + foundUser.id);
    } else if (
      (lowercaseUsername === "admin" ||
        lowercaseUsername === "admin@thirantech.com") &&
      password === "pass1234"
    ) {
      localStorage.setItem("isLoggedIn", true);
      setUserIsLoggedIn(true);
       setSnackbarOpen(true);
      const delayDuration = 1000; // Adjust the duration as needed
      await new Promise((resolve) => setTimeout(resolve, delayDuration));
      navigate("/");
    } else {
      setErrorMessage("Invalid username or password");
      
    }

    // Clear the password in the form data
    setValue("password", "");
  };

  const getProjectData = () => {
    const projectData = JSON.parse(localStorage.getItem("projectData"));

    if (!projectData) {
      const projectsWithUUIDs = dummyProjects.map((project) => ({
        ...project,
        id: uuidv4(),
      }));

      localStorage.setItem("projectData", JSON.stringify(projectsWithUUIDs));
    }
  };

  const getUserData = () => {
    const storedUsers = JSON.parse(localStorage.getItem("userData"));

    if (!storedUsers) {
      const usersWithIds = userArray.map((user) => ({
        ...user,
        id: uuidv4(),
      }));

      localStorage.setItem("userData", JSON.stringify(usersWithIds));
    }
  };

  return (
    <>
      <Login
        handleSubmit={handleSubmit}
        onSubmit={onSubmit}
        register={register}
        setValue={setValue}
        errors={errors}
        errorMessage={errorMessage}
      />
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={handleSnackbarClose}
      >
        <MuiAlert
          elevation={6}
          variant="filled"
          onClose={handleSnackbarClose}
          severity="success"
        >
          Login successful!
        </MuiAlert>
      </Snackbar>
    </>
  );
};

export default LoginContainer;
