import styled from "styled-components";

// Define the button style
const Button = styled.button`
  background-color: #5349d6;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #0056b3;
  }
`;

export default Button;
