import React, { useState } from "react";
import Logout from "../presentation/Logout"; // Import the presentation component
import { useNavigate } from "react-router-dom";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";

const LogoutContainer = () => {
  const navigate = useNavigate();
  const [snackbarOpen, setSnackbarOpen] = useState(false);

  const handleSnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setSnackbarOpen(false);
  };

  const handleLogout = async () => {
    const isAdmin = JSON.parse(localStorage.getItem("isLoggedIn"));
    const isUser = JSON.parse(localStorage.getItem("loggedInUser"));

    if (isUser) {
      localStorage.removeItem("loggedInUser");
      setSnackbarOpen(true);
      const delayDuration = 1000; // Adjust the duration as needed
      await new Promise((resolve) => setTimeout(resolve, delayDuration));
      navigate("/");
    }

    if (isAdmin) {
      localStorage.setItem("isLoggedIn", false);
    setSnackbarOpen(true);
    const delayDuration = 1000; // Adjust the duration as needed
    await new Promise((resolve) => setTimeout(resolve, delayDuration));
      window.location.reload();
    }
  };

  return (
    <>
      <Logout handleLogout={handleLogout} />
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={handleSnackbarClose}
      >
        <MuiAlert
          elevation={6}
          variant="filled"
          onClose={handleSnackbarClose}
          severity="success"
        >
          Logout successful!
        </MuiAlert>
      </Snackbar>
    </>
  );
};

export default LogoutContainer;
